body {
  background-color: darken($dark-blue, 5%);
  font-family: Nunito, sans-serif;
  overflow-x: hidden;

  @media (max-width: 705px) {
    font-size: .8em;
  }
}

.card-container1 {

  .animation-card1 {
    position: relative;
    background-color: lighten($dark-blue, 5%);
    padding: 1em;
    z-index: 5;
    border-radius: 12px;
    box-shadow: 4px 4px 20px rgba(0,0,0,.3);
    transition: 200ms ease-in-out;

    + .animation-card1 {
      margin-top: 1em;
    }

    &:hover {
      z-index: 7;
      transform: scale(1.05);
      box-shadow: 0 4px 25px rgba(0,0,0,.4);
    }

    &:first-of-type {
      margin-left: -3em;
      margin-right: 3em;

      @media (max-width: 1280px) {
        margin-left: -1em;
        margin-right: 1em;
      }

      @media (max-width: 705px) {
        margin-left: -1em;
        margin-right: 1em;
      }

      @media (max-width: 550px) {
        margin: 0;
      }
    }

    &:nth-of-type(2) {
      margin-left: 5em;
      margin-right: -5em;

      @media (max-width: 1280px) {
        margin-left: -1em;
        margin-right: 1em;
      }

      @media (max-width: 705px) {
        margin-left: -1em;
        margin-right: 1em;
      }

      @media (max-width: 550px) {
        margin: 1em 0 0;
      }
    }

    &:nth-of-type(3) {
      margin-left: -1em;
      margin-right: 1em;

      @media (max-width: 1280px) {
        margin-left: -1em;
        margin-right: 1em;
      }

      @media (max-width: 705px) {
        margin-left: -1em;
        margin-right: 1em;
      }

      @media (max-width: 550px) {
        margin: 1em 0 0;
      }
    }

    &:nth-of-type(4) {
      margin-left: 6em;
      margin-right: -4em;

      @media (max-width: 1280px) {
        margin-left: -1em;
        margin-right: 1em;
      }

      @media (max-width: 705px) {
        margin-left: -1em;
        margin-right: 1em;
      }

      @media (max-width: 550px) {
        margin: 1em 0 0;
      }
    }

    &:nth-of-type(5) {
      margin-left: -1em;
      margin-right: -2em;

      @media (max-width: 1280px) {
        margin-left: -1em;
        margin-right: 1em;
      }

      @media (max-width: 705px) {
        margin-left: -1em;
        margin-right: 1em;
      }

      @media (max-width: 550px) {
        margin: 1em 0 0;
      }
    }

    .animation-card1__header {
      display: grid;
      grid-template-columns: 36px auto 100px;
      align-items: center;
      text-transform: uppercase;

      svg {
        color: $yellow;
        margin: 10px;
      }

      span {
        color: $light-blue;
        font-size: .85em;
        font-weight: 600;
        letter-spacing: 1px;
      }

      img {
        margin: 10px;
        color: $light-blue;
        font-size: .85em;
        font-weight: 600;
        text-align: right;
      }
    }

    .animation-card1__body {
      display: flex;
      margin: 0 10px 10px;

      .animation-card1__text {
        margin: 0 10px 0 0;
        white-space: pre-line;
        color: $gray;
        font-weight: 400;
        line-height: 1.5;
        .cardText_highlight {
          color: lighten($yellow, 15%);
        }
      }

      .cardStats {
        font-size: .9em;
        text-align: right;

        .cardStats_stat {
          display: inline-block;
          white-space: nowrap;

          + .cardStats_stat {
            margin-top: 5px;
          }

          &.cardStats_stat-likes {
            color: $light-pink;
          }

          &.cardStats_stat-retweets {
            color: $light-green;
          }

          &.cardStats_stat-shares {
            color: $light-purple;
          }

          &.cardStats_stat-comments {
            color: $light-orange;
          }
        }
      }
    }
  }
}
