.carousel__container {
  @include size(100%, auto);
  display: flex;
  justify-content: center;
  padding: 0px 80px 100px 80px;
}

.carousel__info {
  margin-top: 50px;
  text-align: center;

  p {
    font-family: "Dancing Script", cursive;
    font-size: 20px;
    font-weight: 500;
    color: #08be95;
  }
}

.carousel__area {
  @include size(800px, auto);
  white-space: nowrap;
  position: relative;
  overflow:hidden;
  box-sizing:border-box;

  > .carousel__posts {
    position: relative;
    transition:0.5s;
  }
}

.carousel__postBox {
  @include size(100%);
  display:inline-block;
  box-sizing:border-box;

  > .cover {
    @include size(100%);
    @include trans;
    background-position:center,center;
    background-size:cover;

    &:hover {
        transform: scale(1.1);
    }
  }
}

.control-left , .control-right {
  @include size(40px);
  @include flex_center;
  @include trans;
  border: solid 1px white;
  border-radius: 50%;
  color:white;
  font-size: 30px;
  cursor: pointer;
  position: absolute;
  top:50%;
  transform: translateY(-50%);

  &:hover {
    color:#1c1c1c;
    background-color: rgba(255,255,255,.8);
  }
}

.control-left {
  left: 1rem;
}

.control-right {
  right: 1rem;
}
