.main-content {
    margin-bottom: 15px;
    position: relative;

    .category-list-img {
        margin-top: 10px;
        margin-bottom: 15px;
    }

    .main-content__title {
        font-family: 'Poppins', sans-serif;
        font-style: normal;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 25px;
        line-height: 26px;
        color: white;
        animation: neon 3s infinite;
        margin-top: 50px;
        text-align: center;
        display: block;
        height: auto;
    }

    .main-content__list {
        position: relative;
        max-width: 700px;
        width: 100%;
        margin-top: 70px;
        margin-left: auto;
        margin-right: auto;

        &:before {
          content: '';
          position: absolute;
          width: 120%;
          height: 95%;
          top: -1.5em;
          left: 0em;
          border-radius: 8px;
          @media (max-width: 550px) {
            left: 1.5em;
          }
        }

        .backgroundSVG {
            position: absolute;
            top: 0;
            max-width: 90vw;
            min-width: 500px;
            max-height: 100vh;
            min-height: 725px;
            fill: $pale-red;

            @media (max-width: 705px) {
            display: none;
            }

            path:nth-of-type(1) {
            transform: scale(.3) translateY(250px) translateX(15px);
            }

            path:nth-of-type(2) {
            transform: scale(.3) translateY(230px) translateX(10px);
            }

            path:nth-of-type(3) {
            transform: scale(.3) translateX(256px) translateY(39px);
            }

            path:nth-of-type(4) {
            transform: scale(.3) translateY(175px);
            }
            path:nth-of-type(5) {
            transform: scale(.3) translateX(267px) translateY(50px);
            }

            path:nth-of-type(6) {
            transform: scale(.3) translateY(170px) translateX(10px);
            }

            path:nth-of-type(7) {
            transform: scale(.3) translateY(150px);
            }

            path:nth-of-type(8) {
            transform: scale(.3) translateY(150px);
            }

            path:nth-of-type(9) {
            transform: scale(.3) translateX(230px) translateY(-34px);
            }

            path:nth-of-type(10) {
            transform: scale(.3) translateX(354px) translateY(-35px) rotateY(180deg) rotate(25deg);
            }

            path:nth-of-type(11) {
            transform: scale(.3) translateY(85px) translateX(-15px);
            }
        }
    }

    .action-group {
        position: absolute;
        right: 0;
        top: 20px;

        div {
            font-family: 'Poppins', sans-serif;
            font-style: normal;
            font-weight: bold;
            font-size: 12px;
            line-height: 18px;
            letter-spacing: 0.008em;
            color: #556685;
            cursor: pointer;
            transition: all 0.3s;
        }

        div:hover {
            color: #2f3949;
            svg {
                stroke: #1d242e;
                transition: all 0.3s;
            }
        }

        .action-group__btn-previous {
            margin-right: 20px;
        }
    }
}


@media screen and (max-width: 744px) {
    .react-multi-carousel-track {
        li {
            display: flex;
            justify-content: center;
        }
    }
}

@keyframes neon {
    0% {
      text-shadow: -1px -1px 1px $shadow-color-light, -1px 1px 1px $shadow-color-light, 1px -1px 1px $shadow-color-light, 1px 1px 1px $shadow-color-light,
      0 0 3px $shadow-color-light, 0 0 10px $shadow-color-light, 0 0 20px $shadow-color-light,
      0 0 30px $shadow-color, 0 0 40px $shadow-color, 0 0 50px $shadow-color, 0 0 70px $shadow-color, 0 0 100px $shadow-color, 0 0 200px $shadow-color
    }
    50% {
      text-shadow: -1px -1px 1px $shadow-color-light, -1px 1px 1px $shadow-color-light, 1px -1px 1px $shadow-color-light, 1px 1px 1px $shadow-color-light,
      0 0 5px $shadow-color-light, 0 0 15px $shadow-color-light, 0 0 25px $shadow-color-light,
      0 0 40px $shadow-color, 0 0 50px $shadow-color, 0 0 60px $shadow-color, 0 0 80px $shadow-color, 0 0 110px $shadow-color, 0 0 210px $shadow-color
    }
    100% {
      text-shadow: -1px -1px 1px $shadow-color-light, -1px 1px 1px $shadow-color-light, 1px -1px 1px $shadow-color-light, 1px 1px 1px $shadow-color-light,
      0 0 3px $shadow-color-light, 0 0 10px $shadow-color-light, 0 0 20px $shadow-color-light,
      0 0 30px $shadow-color, 0 0 40px $shadow-color, 0 0 50px $shadow-color, 0 0 70px $shadow-color, 0 0 100px $shadow-color, 0 0 200px $shadow-color
    }
  }
