
@mixin trans($t:0.5s , $tdelay:0s) {
  transition: $t $tdelay;
}

@mixin flex_center {
  display:flex;
  justify-content:center;
  align-items:center;
}

@mixin size($w , $h:$w) {
  width: $w;
  height: $h
}

.saved-card {
  width:100%;
  height:100%;
  display: flex;
  align-items: center;
  background-color: transparent;
  box-sizing: border-box;
  padding: 10px;
}

.saved-card__main {
  display:flex;
  background: #30344c;
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  color: white;
  align-items: center;
  box-shadow: 4px 4px 10px rgba(146, 90, 90, 0.3);

  .saved-card__image {
    width: 100px;
    height: 100px;
    border-radius: 5px;
    @include trans;
    background-position:center,center;
    background-size:cover;
    margin-right: 5px;
  }

  .saved-card__info {
    display:flex;
    flex-direction: column;
    width:calc(100% - 100px - 5px);
    margin-left: 30px;
  }

  .saved-card__value {
    font-size: 20px;
    line-height:20px;
    font-weight: 600;
    white-space: pre-line;
    letter-spacing:1px;
    font-family: 'Bad Script', cursive;
    font-style: italic;
  }
}
