.header {
    margin-top: 50px;
	text-align: center;
	width: 100%;
	height: auto;
	background-size: cover;
	background-attachment: fixed;
	position: relative;
	overflow: hidden;
	border-radius: 0 0 85% 85% / 30%;

    .overlay {
        width: 100%;
        height: 100%;
        padding: 50px;
        color: #FFF;
        text-shadow: 1px 1px 1px #333;
        background-image: linear-gradient( 135deg, #9f05ff69 10%, #fd5e086b 100%);
    }

    .header__logo-div {

        .header__logo {
            cursor: pointer;
        }
    }

    .header__info {
        .header__title {
            font-family: 'Dancing Script', cursive;
            color: #08be95;
            font-weight: bolder;
            font-size: 20px;
        }

        .header__author {
            font-family: 'Dancing Script', cursive;
            color: #be64db;
            font-weight: bolder;
            font-size: 20px;
        }
    }
}
