body {
  font-family: 'Open Sans', sans-serif;
}

.card-container3 {
  width: 800px;
  top: 60px;
  left: calc(50% - 300px);
  display: flex;

  @media (max-width: 1024px) {
    width: 100%;
    display: block;
  }

  .card3 {
    height: 280px;
    width: 200px;
    background-color: #17141d;
    border-radius: 10px;
    box-shadow: -1rem 0 3rem #000;
    transition: 0.4s ease-out;
    position: relative;
    left: 0px;

    @media (max-width: 1024px) {
      width: 100%;
      height: fit-content;
      padding-bottom: 20px;
      text-align: center;
    }

    .card3__icon {
      width: 100%;
      text-align: center;
      padding-top: 20px;
    }

    .card3__title {
      margin-top: 20px;

      .title {
        color: white;
        font-weight: 100;
      }
    }
  }

  .card3:not(:first-child) {
    margin-left: -50px;

    @media (max-width: 1024px) {
      margin-left: 0px;
    }
  }

  .card3:hover {
    transform: translateY(-20px);
    transition: 0.4s ease-out;
  }

  .card3:hover ~ .card3 {
    position: relative;
    left: 50px;
    transition: 0.4s ease-out;
  }

  .card3__bar {
    position: absolute;
    bottom: 20px;
    left: 20px;
    height: 5px;
    width: 150px;

    @media (max-width: 1024px) {
      display: none;
    }

    .emptybar {
      background-color: #2e3033;
      width: 100%;
      height: 100%;
    }

    .filledbar {
      position: absolute;
      top: 0px;
      z-index: 3;
      width: 0px;
      height: 100%;
      background: rgb(0,154,217);
      background: linear-gradient(90deg, rgba(0,154,217,1) 0%, rgba(217,147,0,1) 65%, rgba(255,186,0,1) 100%);
      transition: 0.6s ease-out;
    }
  }

  .card3:hover .filledbar {
    width: 150px;
    transition: 0.4s ease-out;
  }

  .card3__value {
    color: white;
    margin: 0px 20px;
    max-height: 150px;
    overflow-y: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
  }

  /* Hide scrollbar for Chrome, Safari and Opera */
  .card3__value::-webkit-scrollbar {
    display: none;
  }
}