.d-flex-center {
    display: flex;
    align-items: center;
}

.justify-between {
    justify-content: space-between;
}

.d-grid {
    display: grid;
}

.default-layout {
    padding: 0% 15% 10% 15%;
    position: relative;
}

.content-layout {
    margin-top: 40px;
    display: flex;

    .content-layout__sidebar {
        width: 25%;
    }

    .content-layout__body {
        width: 75%;
        padding: 0 15px;
        margin-bottom: 40px;
    }
}

::-webkit-scrollbar-track {
    border-radius: 10px;
    background: #e4e4e4;
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: #b0b0b0;
}

::-webkit-scrollbar {
    height: 6px;
    width: 6px;
    border: 1px solid #d5d5d5;
}

@media screen and (max-width: 1320px) {
    .default-layout {
        padding: 0 10%;
    }
}

@media screen and (max-width: 1040px) {
    .content-layout {

        .content-layout__sidebar {
            width: 40%;
        }

        .content-layout__body {
            width: 60%;
        }
    }
}

@media screen and (max-width: 640px) {
    .content-layout {
        display: block;
        .content-layout__sidebar {
            width: 100%;
        }

        .content-layout__body {
            width: 95%;
        }
    }
}

@media screen and (max-width: 512px) {
    .default-layout {
        padding: 0 1%;
    }
}
