.categories {
    padding: 30px 0px;

    .card-category {
        padding: 10px;
        width: 50%;
        margin: 20px 15px;
        background: #FFFFFF;
        box-shadow: 0px 1px 4px rgba(0, 8, 32, 0.12), 0px 4px 8px rgba(0, 8, 32, 0.08);
        border-radius: 4px;
        cursor: pointer;
        margin-left: auto;
        margin-right: auto;
        text-align: center;

        a {
            text-decoration: none;
        }

        p {
            margin: 0;
            font-family: 'Poppins', sans-serif;
            font-style: normal;
            font-size: 16px;
            line-height: 26px;
        }

        .card-category__title {
            font-weight: bold;
        }

        .card-category__value {
            font-weight: normal;
            color: #556685;
        }
    }

    .select {
        p {
            color: #ffffff!important;
        }

        svg {
            stroke: #ffffff!important;
        }
    }


}

@media screen and (max-width: 1024px) {
    .tabs {
        display: block;

        .card-category {
            width: auto!important;
        }
    }
}
